
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"20px","background":"none","width":"1200px","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator="/" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }">首页</el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="12">
					<el-form-item label="实验室名称" prop="shiyanshimingcheng">
						<el-input class="list_inp" v-model="form.shiyanshimingcheng" placeholder="实验室名称"
							 type="text" 							:readonly="!isAdd||disabledForm.shiyanshimingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="实验室位置" prop="shiyanshiweizhi">
						<el-input class="list_inp" v-model="form.shiyanshiweizhi" placeholder="实验室位置"
							 type="text" 							:readonly="!isAdd||disabledForm.shiyanshiweizhi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="实验室规模" prop="shiyanshiguimo">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.shiyanshiguimo?true:false"
							v-model="form.shiyanshiguimo" 
							placeholder="请选择实验室规模"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in shiyanshiguimoLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="容纳人数" prop="rongnarenshu">
						<el-input class="list_inp" v-model.number="form.rongnarenshu" placeholder="容纳人数"
							 type="text" 							:readonly="!isAdd||disabledForm.rongnarenshu?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="设备数量" prop="shebeishuliang">
						<el-input class="list_inp" v-model.number="form.shebeishuliang" placeholder="设备数量"
							 type="text" 							:readonly="!isAdd||disabledForm.shebeishuliang?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="图片" prop="tupian">
						<uploads
							:disabled="!isAdd||disabledForm.tupian?true:false"
							action="file/upload" 
							tip="请上传图片" 
							:limit="3" 
							style="width: 100%;text-align: left;"
							:fileUrls="form.tupian?form.tupian:''" 
							@change="tupianUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="实验室类型" prop="shiyanshileixing">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.shiyanshileixing?true:false"
							v-model="form.shiyanshileixing" 
							placeholder="请选择实验室类型"
							style="width:100%;"
							>
							<el-option v-for="(item,index) in shiyanshileixingLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="设备" prop="shebei">
						<el-input v-model="form.shebei" placeholder="设备" type="textarea"
						:readonly="!isAdd||disabledForm.shebei?true:false"
						/>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="设施" prop="sheshi">
						<el-input v-model="form.sheshi" placeholder="设施" type="textarea"
						:readonly="!isAdd||disabledForm.sheshi?true:false"
						/>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="试剂" prop="shiji">
						<el-input v-model="form.shiji" placeholder="试剂" type="textarea"
						:readonly="!isAdd||disabledForm.shiji?true:false"
						/>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="耗材" prop="haocai">
						<el-input v-model="form.haocai" placeholder="耗材" type="textarea"
						:readonly="!isAdd||disabledForm.haocai?true:false"
						/>
					</el-form-item>
				</el-col>
				<el-col :span="24">
					<el-form-item label="使用说明" prop="shiyongshuoming">
						<editor class="list_editor" :value="form.shiyongshuoming" placeholder="请输入使用说明" :readonly="!isAdd||disabledForm.shiyongshuoming?true:false"
							@change="(e)=>editorChange(e,'shiyongshuoming')"></editor>
					</el-form-item>
				</el-col>
			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick">取消</el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					保存
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//基础信息
	const tableName = 'shiyanshixinxi'
	const formName = '实验室信息'
	//基础信息
	const breadList = ref([{
		name: formName
	}])
	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form表单
	const form = ref({
		shiyanshimingcheng: '',
		shiyanshiweizhi: '',
		shiyanshiguimo: '',
		rongnarenshu: '',
		shebei: '',
		shebeishuliang: '',
		sheshi: '',
		shiji: '',
		haocai: '',
		tupian: '',
		shiyanshileixing: '',
		shiyongshuoming: '',
		storeupnum: '0',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		shiyanshimingcheng : false,
		shiyanshiweizhi : false,
		shiyanshiguimo : false,
		rongnarenshu : false,
		shebei : false,
		shebeishuliang : false,
		sheshi : false,
		shiji : false,
		haocai : false,
		tupian : false,
		shiyanshileixing : false,
		shiyongshuoming : false,
		storeupnum : false,
	})
	const isAdd = ref(false)
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		shiyanshimingcheng: [
		],
		shiyanshiweizhi: [
		],
		shiyanshiguimo: [
		],
		rongnarenshu: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		shebei: [
		],
		shebeishuliang: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		sheshi: [
		],
		shiji: [
		],
		haocai: [
		],
		tupian: [
		],
		shiyanshileixing: [
		],
		shiyongshuoming: [
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
	})
	//实验室规模列表
	const shiyanshiguimoLists = ref([])
	//图片上传回调
	const tupianUploadSuccess=(e)=>{
		form.value.tupian = e
	}
	//实验室类型列表
	const shiyanshileixingLists = ref([])
	//methods

	//methods
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.shiyongshuoming = res.data.data.shiyongshuoming.replace(reg,'../../../cl97931174/file');
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='shiyanshimingcheng'){
					form.value.shiyanshimingcheng = row[x];
					disabledForm.value.shiyanshimingcheng = true;
					continue;
				}
				if(x=='shiyanshiweizhi'){
					form.value.shiyanshiweizhi = row[x];
					disabledForm.value.shiyanshiweizhi = true;
					continue;
				}
				if(x=='shiyanshiguimo'){
					form.value.shiyanshiguimo = row[x];
					disabledForm.value.shiyanshiguimo = true;
					continue;
				}
				if(x=='rongnarenshu'){
					form.value.rongnarenshu = row[x];
					disabledForm.value.rongnarenshu = true;
					continue;
				}
				if(x=='shebei'){
					form.value.shebei = row[x];
					disabledForm.value.shebei = true;
					continue;
				}
				if(x=='shebeishuliang'){
					form.value.shebeishuliang = row[x];
					disabledForm.value.shebeishuliang = true;
					continue;
				}
				if(x=='sheshi'){
					form.value.sheshi = row[x];
					disabledForm.value.sheshi = true;
					continue;
				}
				if(x=='shiji'){
					form.value.shiji = row[x];
					disabledForm.value.shiji = true;
					continue;
				}
				if(x=='haocai'){
					form.value.haocai = row[x];
					disabledForm.value.haocai = true;
					continue;
				}
				if(x=='tupian'){
					form.value.tupian = row[x];
					disabledForm.value.tupian = true;
					continue;
				}
				if(x=='shiyanshileixing'){
					form.value.shiyanshileixing = row[x];
					disabledForm.value.shiyanshileixing = true;
					continue;
				}
				if(x=='shiyongshuoming'){
					form.value.shiyongshuoming = row[x];
					disabledForm.value.shiyongshuoming = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.storeupnum='0'
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
		})
		shiyanshiguimoLists.value = "大型,中型,小型".split(',')
		context?.$http({
			url: `option/shiyanshileixing/shiyanshileixing`,
			method: 'get'
		}).then(res=>{
			shiyanshileixingLists.value = res.data.data
		})
	}
	//初始化
	//取消
	const backClick = () => {
		history.back()
	}
	//富文本数据回调
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		if(form.value.tupian!=null) {
			form.value.tupian = form.value.tupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 面包屑盒子
	.bread_view {
		border-radius: 0px;
		padding: 0px 20px 10px;
		margin: 0px auto;
		background: none;
		width: calc(100% - 0px);
		border-color: #66666650;
		border-width: 0 0 1px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #333;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
		}
	}
	// 表单
	.add_form{
		border: 0px solid #27bacc30;
		border-radius: 0px;
		padding: 30px 7%;
		background: none;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 150px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					padding: 0 10px;
					background: #fff;
					width: 100%;
					border-color: #ddd;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 350px;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				// 下拉框
				.list_sel {
					border-radius: 0px;
					padding: 0 10px;
					background: #fff;
					width: 100%;
					border-color: #ddd;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 300px;
					//去掉默认样式
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
							.el-input__wrapper{
								border: none;
								box-shadow: none;
								background: none;
								border-radius: 0;
								height: 100%;
								padding: 0;
							}
							.is-focus {
								box-shadow: none !important;
							}
						}
					}
				}
				// 富文本
				.list_editor {
					border-radius: 0;
					padding: 0;
					margin: 0;
					background: #fff;
					width: 99%;
					min-height: 250px;
					border-color: #eee;
					border-width: 1px;
					border-style: solid;
					height: auto;
				}
				// 长文本
				.el-textarea__inner {
					border: 1px solid #ddd;
					border-radius: 2px;
					padding: 12px;
					color: #666;
					background: #fff;
					width: 100%;
					font-size: 14px;
					min-height: 120px;
				}
				//图片上传样式
				.el-upload-list  {
					//提示语
					.el-upload__tip {
						margin: 7px 0 0;
						color: #999;
						display: flex;
						font-size: 14px;
						justify-content: flex-start;
						align-items: center;
					}
					//外部盒子
					.el-upload--picture-card {
						border: 1px solid #ddd;
						cursor: pointer;
						border-radius: 2px;
