import { createRouter, createWebHashHistory } from 'vue-router'
import index from '../views'
import home from '../views/pages/home.vue'
import login from '../views/pages/login.vue'
import newsList from '@/views/pages/news/list'
import shiyanshixinxiList from '@/views/pages/shiyanshixinxi/list'
import shiyanshixinxiDetail from '@/views/pages/shiyanshixinxi/formModel'
import shiyanshixinxiAdd from '@/views/pages/shiyanshixinxi/formAdd'
import storeupList from '@/views/pages/storeup/list'
import shiyanshileixingList from '@/views/pages/shiyanshileixing/list'
import shiyanshileixingDetail from '@/views/pages/shiyanshileixing/formModel'
import shiyanshileixingAdd from '@/views/pages/shiyanshileixing/formAdd'
import kechengxinxiList from '@/views/pages/kechengxinxi/list'
import kechengxinxiDetail from '@/views/pages/kechengxinxi/formModel'
import kechengxinxiAdd from '@/views/pages/kechengxinxi/formAdd'
import xueshengchengjiList from '@/views/pages/xueshengchengji/list'
import xueshengchengjiDetail from '@/views/pages/xueshengchengji/formModel'
import xueshengchengjiAdd from '@/views/pages/xueshengchengji/formAdd'
import xueshengList from '@/views/pages/xuesheng/list'
import xueshengDetail from '@/views/pages/xuesheng/formModel'
import xueshengAdd from '@/views/pages/xuesheng/formAdd'
import xueshengRegister from '@/views/pages/xuesheng/register'
import xueshengCenter from '@/views/pages/xuesheng/center'
import daoshixinxiList from '@/views/pages/daoshixinxi/list'
import daoshixinxiDetail from '@/views/pages/daoshixinxi/formModel'
import daoshixinxiAdd from '@/views/pages/daoshixinxi/formAdd'

const routes = [{
		path: '/',
		redirect: '/index/home'
	},
	{
		path: '/index',
		component: index,
		children: [{
			path: 'home',
			component: home
		}
		, {
			path: 'newsList',
			component: newsList
		}
		, {
			path: 'shiyanshixinxiList',
			component: shiyanshixinxiList
		}, {
			path: 'shiyanshixinxiDetail',
			component: shiyanshixinxiDetail
		}, {
			path: 'shiyanshixinxiAdd',
			component: shiyanshixinxiAdd
		}
		, {
			path: 'storeupList',
			component: storeupList
		}
		, {
			path: 'shiyanshileixingList',
			component: shiyanshileixingList
		}, {
			path: 'shiyanshileixingDetail',
			component: shiyanshileixingDetail
		}, {
			path: 'shiyanshileixingAdd',
			component: shiyanshileixingAdd
		}
		, {
			path: 'kechengxinxiList',
			component: kechengxinxiList
		}, {
			path: 'kechengxinxiDetail',
			component: kechengxinxiDetail
		}, {
			path: 'kechengxinxiAdd',
			component: kechengxinxiAdd
		}
		, {
			path: 'xueshengchengjiList',
			component: xueshengchengjiList
		}, {
			path: 'xueshengchengjiDetail',
			component: xueshengchengjiDetail
		}, {
			path: 'xueshengchengjiAdd',
			component: xueshengchengjiAdd
		}
		, {
			path: 'xueshengList',
			component: xueshengList
		}, {
			path: 'xueshengDetail',
			component: xueshengDetail
		}, {
			path: 'xueshengAdd',
			component: xueshengAdd
		}
		, {
			path: 'xueshengCenter',
			component: xueshengCenter
		}
		, {
			path: 'daoshixinxiList',
			component: daoshixinxiList
		}, {
			path: 'daoshixinxiDetail',
			component: daoshixinxiDetail
		}, {
			path: 'daoshixinxiAdd',
			component: daoshixinxiAdd
		}
		]
	},
	{
		path: '/login',
		component: login
	}
	,{
		path: '/xueshengRegister',
		component: xueshengRegister
	}
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
